import axios from 'axios'
import { FETCH_DATA } from '../consts.js'

export const fetchData = () => async dispatch => {
    const response = await axios({
        method: "GET",
        url: 'http://127.0.0.1:8000/api/group'
    })

    dispatch({
        type: FETCH_DATA,
        payload: response.data
    })
}


